import { Injectable } from '@angular/core';
import { FiltersApi } from '@core/data-access/apis/filters.api';
import { PropertyWorthsApi } from '@core/data-access/apis/property-worth.api';
import { SectionsState } from '@core/data-access/states/sections.state';
import { AisleType, SectionType } from '@core/enums';
import { FilterValue } from '@core/models';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class RentalEstimateToolFacade {
	constructor(
		private sectionState: SectionsState,
		private readonly filterApi: FiltersApi,
		private readonly propertyWorth: PropertyWorthsApi
	) {}
	private readonly _isProcessing$ = new BehaviorSubject<boolean>(false);

	get isProcessing$(): Observable<boolean> {
		return this._isProcessing$.asObservable();
	}

	setIsProcessing(status: boolean): void {
		this._isProcessing$.next(status);
	}

	getFilterUnits(aisle: AisleType = AisleType.RENT_RESIDENTIAL) {
		return this.filterApi.getFilterValue('property-units', { aisle });
	}

	getSectionList(section: SectionType = SectionType.PROPERTIES_FOR_RENT) {
		return this.sectionState.getSortedAisleList$(section);
	}

	getTenantList(aisle: AisleType = AisleType.RENT_RESIDENTIAL): Observable<FilterValue> {
		return this.filterApi.getFilterValue('tenants', { aisle, section: SectionType.PROPERTIES_FOR_RENT });
	}

	getRoomTypes(): Observable<FilterValue> {
		return this.filterApi.getFilterValue('room-types');
	}

	postRentalProperty(data) {
		return this.propertyWorth.postRentalEstimate(data);
	}
}
