import { Component, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatInput } from '@angular/material/input';
import { AuthService } from '@core/data-access/services/auth.service';
import { SnackService } from '@core/services/snack.service';
import { ButtonType } from '@shared/components/button/button.component';
import { PhoneNumberValidation } from '@core/validators/phone.validator';

@Component({
    selector: 'findqo-about-you',
    templateUrl: './about-you.component.html',
    styleUrls: ['./about-you.component.scss'],
    standalone: false
})
export class AboutYouComponent implements OnInit {
	constructor(
		private fb: FormBuilder,
		private auth: AuthService,
		private snack: SnackService,
		private phoneValidation: PhoneNumberValidation
	) {}
	@Output() nextTab = new EventEmitter();
	@Output() prevTab = new EventEmitter();
	@ViewChild('phoneNumber') phoneNumber?: MatInput;
	readonly buttonType = ButtonType;

	aboutYouForm = this.fb.group({
		fullname: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(80)]],
		email: ['', [Validators.required, Validators.email]],
		phoneCode: ['+353', [Validators.required]],
		phone: ['', [Validators.required, this.phoneValidation.phoneValidator(true)]],
		isPersonValuation: ['', [Validators.required]],
	});

	onMouseClick() {
		setTimeout(() => {
			this.phoneNumber?.focus();
		}, 100);
	}

	onNext() {
		if (this.aboutYouForm.invalid) {
			return;
		}

		this.nextTab.emit({ status: this.aboutYouForm.status, value: this.aboutYouForm.value, form: 'aboutYou' });
	}

	onBack() {
		this.prevTab.emit();
	}

	onUpdateRadioBtn(type: string) {
		this.aboutYouForm.get('isPersonValuation').setValue(type);
	}

	ngOnInit(): void {
		const USER_PAYLOAD = this.auth.user();

		if (USER_PAYLOAD && USER_PAYLOAD?.name) {
			this.aboutYouForm.patchValue({
				fullname: USER_PAYLOAD?.name,
				email: USER_PAYLOAD?.email,
				phoneCode: USER_PAYLOAD?.phoneCode || '+353',
				phone: USER_PAYLOAD?.phone,
			});

			this.aboutYouForm.markAllAsTouched();
		}
	}
}
